@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@700&display=swap');

$grayBlue: hsl(237, 18%, 59%);
$red: hsl(345, 95%, 68%);
$white: hsl(0, 0%, 100%);
$darkDesBlue: hsl(236, 21%, 26%);
$VeryDarkBlue: hsl(235, 16%, 14%);
$blackBlue: hsl(234, 17%, 12%);

html {
    font-size: 62.5%;

    @media only screen and(max-width: 900px) {
        font-size: 45%;
    }

    @media only screen and(max-width: 600px) {
        font-size: 29%;
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;

    position: relative;

    font-size: 1.4rem;
    font-family: 'Red Hat Text', sans-serif;

    background-image: url("./images/bg-stars.svg"), url("./images/pattern-hills.svg");
    background-size: cover, contain;

    @media only screen and(max-width: 600px) {
        background-size: cover, 250%;
    }

    @media only screen and(max-width: 1200px) {
        background-size: cover, 200%;
    }

    background-repeat: no-repeat;
    background-position-y: 0, bottom;
    background-color: $VeryDarkBlue;
}

.container {
    max-width: 100rem;
    text-align: center;
    padding: 10rem 0;
    margin: 0 auto;

    &__title {
        font-size: 2.4rem;
        color: $white;
        text-transform: uppercase;
        letter-spacing: 1rem;

        padding-bottom: 10rem;

        @media only screen and(max-width: 600px) {
            padding-top: 15rem;
            font-size: 5.4rem;
            margin-bottom: 5rem;
        }

        @media only screen and(max-width: 1200px) {
            padding-top: 15rem;
            margin-bottom: 5rem;
            font-size: 5.4rem;
        }
    }

    &__cards {
        display: flex;
        justify-content: space-around;
    }
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: center;

    &__body {
        width: 17rem;
        height: 17rem;
        border-radius: 0.7rem;

        background-color: $darkDesBlue;
        box-shadow: 0 1.3rem $blackBlue;

       position: relative;

       overflow: hidden;
    }

    &__overlay {
        width: 100%;
        height: 50%;

        position: absolute;
        z-index: 2;

        background-color: $blackBlue;
        opacity: 0.3;
    }

    &__number {
        font-size: 8rem;

        background-image: linear-gradient(to bottom right, red 0%, red 20%, white 20%, white 40%, red 40%, red 60%, white 60%, white 80%, red 80%, red 100%);
        background-clip: text;
        color: transparent;

        position: absolute;
        z-index: 1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &__title {
        font-size: 1.7rem;
        color: $white;
        text-transform: uppercase;
        letter-spacing: .5rem;

        opacity: 0.5;

        margin-top: 3rem;

        @media only screen and(max-width: 900px) {
            margin-bottom: 3rem;
        }
    }
}

.ball {
    &1, &2 {
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
        background-color: $blackBlue;

        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 3;
    }

    &2 {
        top: 50%;
        right: 0;
        transform: translate(50%, -50%);
    }
}

.santa {
    max-width: 100vw;
    height: 100vh;
    background-image: linear-gradient(to bottom right, red 0%, red 20%, white 20%, white 40%, red 40%, red 60%, white 60%, white 80%, red 80%, red 100%);
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: absolute;
    z-index: 10;

    text-align: center;

    &-text {
        color: $blackBlue;
        font-size: 10rem;
    }

    &-close {
        color: $blackBlue;
        font-size: 4rem;
        transform: translateY(10vh);
        cursor: pointer;

        transition: all .2s ease;

        &:hover {
            color: $red;
        }
    }

    opacity: 0;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: center;
    
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -100%);

    &__icon {
        font-size: 3rem;

        @media only screen and(max-width: 600px) {
            font-size: 5rem;
        }
        height: auto;
        color: $blackBlue;
        cursor: pointer;
        transition: all .2s ease;

        &:not(:last-child) {
            margin-right: 4rem;
        }

        &:hover {
            color: $red;
            
        }
    }
}



